import React from 'react'
import '../../styles/index/aboutUs.css'
import { Link } from 'gatsby'

export default function AboutUs() {
    return (
        <div className='about-us container-xxl'>
            <div className="row">
                <div className="col-md-8 px-3 mb-4">
                    <h2 className='title'>ABOUT US</h2>
                    <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Beatae esse eos porro exercitationem quam? Beatae iste fugit nemo modi excepturi distinctio corrupti nam sunt labore. Facere libero illum impedit qui.
                        Sequi at perferendis ducimus pariatur doloremque explicabo dolor facere a laudantium minus id, quaerat odit cumque. Adipisci beatae molestias quia ipsam. Quas rerum deserunt corrupti numquam reiciendis ullam neque molestias!</p>
                </div>
                <div className="platforms px-3 col-md-4">
                    <h2 className='title'>PLATFORMS</h2>
                    <Link to='/'><p>PC (Steam, Origin...)</p></Link>
                    <Link to='/'><p>Playstation 4/ Playstation 5</p></Link>
                    <Link to='/'><p>XBOX One / X</p></Link>
                    <Link to='/'><p>Nintendo</p></Link>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <div className="social-media">
                        <Link to='/'><img src="/qkzclan/images/youtube.svg" alt="youtube" /></Link>
                        <Link to='/'><img src="/qkzclan/images/twitch.svg" alt="twitch" /></Link>
                        <Link to='/'><img src="/qkzclan/images/instagram.svg" alt="instagram" /></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
