import { Link } from 'gatsby'
import React from 'react'
import '../../styles/index/mainSlideItem.css'

export default function MainSlideItem(props) {
  return (
    <div className='main-slide-item'>
      <div className="container px-5 px-xxl-0">
        <img src={props.backgroundImage} alt={props.alt} />
        <Link to='/'>{props.linkText}</Link>
        <h1>{props.titleText}</h1>
      </div>
    </div>
  )
}
