import React, {useState, useEffect} from 'react'
import { Link } from 'gatsby';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import '../../styles/index/videoSlider.css'

export default function VideoSlider() {
    const [slideNumber, setSlideNumber] = useState(3);
    useEffect(() => {
        function handleResize() {
            if (window.innerWidth < 1275) setSlideNumber(2)
            else setSlideNumber(3);
            if (window.innerWidth < 885) setSlideNumber(1);            
        }
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);
    var settings = {
        infinite: true,
        speed: 500,
        slidesToShow: slideNumber,
        slidesToScroll: slideNumber,
        autoplay: true,
        arrows: true        
    }
    return (
        <div className="video-slider-wrapper px-1 px-xxl-0">
            <div className='container-xxl'>
                <h2 className='title'>VIDEO NEWS</h2>
                <Slider {...settings}>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news1.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news2.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news3.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news1.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news2.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <Link to='/'><img src="/qkzclan/images/play.svg" alt="play" /></Link>
                        <img src="/qkzclan/images/video-news3.png" alt="video news" />
                    </div>
                </Slider>
            </div>
        </div>
    )
}
