import React, {useState, useEffect} from 'react'
import { Link } from 'gatsby';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import '../../styles/index/secondSlider.css';

export default function SecondSlider() {
  const [slideNumber, setSlideNumber] = useState(4);
  useEffect(() => {
    function handleResize() {
      if (window.innerWidth < 1250) setSlideNumber(3)
        else setSlideNumber(4);
      if (window.innerWidth < 975) setSlideNumber(2);
      if (window.innerWidth < 685) setSlideNumber(1);
    }
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: slideNumber,
    slidesToScroll: slideNumber,
    autoplay: true,
    arrows: true
  }
  return (    
      <div className='second-slider container-xxl px-3 px-xxl-0'>
        <Slider {...settings}>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/crysis.png" alt="crysis" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/destiny2.png" alt="destiny 2" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/transformers.png" alt="transformers" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/tomb-raider.png" alt="tomb raider" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/crysis.png" alt="crysis" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/destiny2.png" alt="destiny 2" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/transformers.png" alt="transformers" /></Link>
          </div>
          <div className="second-slide-item">
            <Link to='/'><img src="/qkzclan/images/tomb-raider.png" alt="tomb raider" /></Link>
          </div>
        </Slider>
      </div>    
  )
}
