import React, { useState } from 'react'
import '../../styles/index/trandingSection.css'
import { Link } from 'gatsby'
import TypeCard from '../TypeCard'

export default function TrandingSection() {
    const [mouseOver, setMouseOver] = useState();
    const toggleTopGame = (args) => {
        setMouseOver(args);
    }
    return (
        <div className="tranding-section container-xxl">
            <div className="row">
                <div className="col center col-xl-4">
                    <h2 className='title'>TRANDING NEWS</h2>
                </div>
                <div className="offset-3 col-5">
                    <h2 className='title'>TOP GAMES</h2>
                </div>
            </div>
            <div className="row gx-5 px-2 px-xxl-0">
                <div className="col-md-6 col-xl-4 tranding-news-col mb-4 mb-md-0">
                    <Link to="/"><img src="/qkzclan/images/avengers.png" alt="avengers" /></Link>
                </div>
                <div className="col-md-6 col-xl-3 mt-1 mt-sm-0 tranding-card-col">
                    <div className='card-wrapper'>
                        <TypeCard
                            imgSource='/qkzclan/images/action.png'
                            imgAlt='action'
                            titleColor='red'
                            cardTitle='ACTION'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                            cardMargin='true'
                        />
                        <TypeCard
                            imgSource='/qkzclan/images/classic.png'
                            imgAlt='action'
                            titleColor='orange'
                            cardTitle='CLASSIC'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                            cardMargin='true'
                        />
                    </div>
                    <div className='card-wrapper'>
                        <TypeCard
                            imgSource='/qkzclan/images/3d-video.png'
                            imgAlt='action'
                            titleColor='blue'
                            cardTitle='3D VIDEO'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                            cardMargin='true'
                        />
                        <TypeCard
                            imgSource='/qkzclan/images/porn.png'
                            imgAlt='action'
                            titleColor='pink'
                            cardTitle='PORN'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                        />
                    </div>
                </div>
                <div className="col col-xl-5 top-games-col mt-5 mt-xl-0">
                    <h2 className='title'>TOP GAMES</h2>
                    <div className="top-games">
                        <div className="row mb-sm-4 text-center">
                            <div className="col col-sm-6 mb-4 top-games-div">
                                <Link to="/">
                                    <img className={mouseOver === 'over' ? 'img-fluid animate__animated animate__pulse animate__faster' : 'img-fluid'} onMouseLeave={() => toggleTopGame('')} onMouseOver={() => toggleTopGame('over')} src="/qkzclan/images/top-game1.png" alt="top game" />
                                </Link>
                            </div>
                            <div className="col col-sm-6 mb-4 top-games-div">
                                <Link to="/">
                                    <img className={mouseOver === 'over1' ? 'img-fluid animate__animated animate__pulse animate__faster' : 'img-fluid'} onMouseLeave={() => toggleTopGame('')} onMouseOver={() => toggleTopGame('over1')} src="/qkzclan/images/top-game2.png" alt="top game" />
                                </Link>
                            </div>
                        </div>
                        <div className="row text-center">
                            <div className="col col-sm-6 mb-4 top-games-div">
                                <Link to="/">
                                    <img className={mouseOver === 'over2' ? 'img-fluid animate__animated animate__pulse animate__faster' : 'img-fluid'} onMouseLeave={() => toggleTopGame('')} onMouseOver={() => toggleTopGame('over2')} src="/qkzclan/images/top-game3.png" alt="top game" />
                                </Link>
                            </div>
                            <div className="col col-sm-6 mb-4 top-games-div">
                                <Link to="/">
                                    <img className={mouseOver === 'over3' ? 'img-fluid animate__animated animate__pulse animate__faster' : 'img-fluid'} onMouseLeave={() => toggleTopGame('')} onMouseOver={() => toggleTopGame('over3')} src="/qkzclan/images/top-game4.png" alt="top game" />
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
