import React from 'react'
import MainSlideItem from "./MainSlideItem";
import '../../styles/index/mainSlider.css'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';

export default function MainSlider() {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        arrows: false,
        className: 'main-slider'
    }
    return (
        <Slider {...settings}>
            <MainSlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <MainSlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <MainSlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <MainSlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
        </Slider>
    )
}
